<template>
    <div id="Pagination" class="layout">
      <p class="tit">分页器1（Pagination）</p>
      <div class="base_box">
        <Button
              :butContent="'模拟获取新数据重置分页器'"
              :Width="'300px'"
              :BGcolor="'#fff'"
              :lineHeight="30"
              :FontColor="'#00f'"
              @click="setcurrentPage"
        />
       <Pagination
            :Count="bigCount"
            :currentPage="currentPage"
            @getCurrentPage="getpage"
       />
      </div>
    </div>
</template>

<script>
import { ref , getCurrentInstance } from "vue";
export default {
    setup(props) {
        const { proxy } = getCurrentInstance();
        const currentPage = ref(1)   //当前选中页数
        const bigCount = ref(30)
        const minCount = ref(20)
        // 分页1
        const getpage = function (val) {
            currentPage.value = val;
            // 自定义弹框
            proxy.h_tips({
                type:'success',
                msg: `点击了第${val}页`,
                top:30,
                time:1000
            })
        }
        // 重置分页器当前页面值
        const setcurrentPage = function() {
            proxy.h_tips({
                type:'success',
                msg: `重置了50页`,
                top:30,
                time:1000
            })
            bigCount.value = 50
            currentPage.value = 1; //重置当前页
        }
        return{
            currentPage,
            bigCount,
            minCount,
            getpage,
            setcurrentPage
        }
    }
   
 
}
</script>

