<template>
  <div id="Tips" class="layout">
    <p class="tit">信息提示框（$tips）</p>
    <Button
      :butContent="'信息提示'"
      :float="'l'"
      :type="'success'"
      :Width="'40%'"
      @click="tipsFn()"
    />
    <Button
      :butContent="'标题信息提示'"
      :float="'r'"
      :type="'success'"
      :Width="'40%'"
      @click="tipsFn1()"
    />
  </div>
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    // 信息提示框按键展示
    const tipsFn = function () {
      // 自定义弹框
      proxy.h_tips({
        type: "success",
        msg: "提示信息文字",
        top: 100,
        time: 100,
      });
    };
    // 标题信息提示框按键展示
    const tipsFn1 = function () {
      // 自定义弹框
      proxy.h_tips({
        type: "success",
        tit: "提示标题",
        msg: "提示信息文字",
        top: 30,
        time: 1000,
      });
    };
    return {
      tipsFn,
      tipsFn1,
    };
  },
};
</script>

<style lang="less"></style>
