<template>
    <div id="Button" class="layout">
      <p class="tit">信息提示框（$tips）</p>
            <Button
              :butContent="'灰色'"
              :type="'info'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="'绿色'"
              :type="'success'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="'黄色'"
              :type="'warning'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="'默认'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="'红色'"
              :type="'error'"
              :float="'l'"
              @click="butfn"
            />
    </div>
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup(props) {
     const { proxy } = getCurrentInstance();
     const butfn =function (type){
        // 自定义弹框
        proxy.h_tips({
            type:type,
            msg: type+ " 类型",
            top:30,
            time:1000
        })
      }
      return{
        butfn
      }
  }
}
</script>

